:root {
    font-size: 16px;
    font-family: 'Open Sans';
    --text-primary: #b6b6b6;
    --text-secondary: #ececec;
    --bg-primary: #23232e;
    --bg-secondary: #141418;
    --transition-speed: 600ms
  }

/* NavBar Start */
.navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 200ms ease;
  }
  
.navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
.nav-item {
    width: 100%;
  }
  
.nav-item:last-child {
    margin-top: auto;
  }
  
.nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7) brightness(5);
    transition: var(--transition-speed);
  }
  
.nav-link svg {
    min-width: 2rem;
    margin: 0 1.5rem;
  }
  
.icon-color {
    color: rgb(69, 162, 224);
  }
  
.nav-link:hover {
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
  }
  
.logo svg {
    transform: rotate(0deg);
    transition: transform var(--transition-speed);
  }
  
.navbar:hover .logo svg {
    transform: rotate(-180deg);
  
  }
  
.link-text {
    display: none;
    margin-left: 1rem;
  }
  
.nav-link svg {
    min-width: 2rem;
    margin: 0 1.5rem;
  }
  
  
  
  
.logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
}
  
  /* MOBILE */
  
  @media only screen and (max-width: 600px) {
  
    .navbar {
      bottom: 0;
      width: 100vw;
      height: 5rem;
    }
  
    .logo {
      display: none;
    }
  
    .navbar-nav {
      flex-direction: row;
    }
  
    .nav-link {
      justify-content: center;
    }
  
    main {
      margin: auto;
    }
  
  }
  /* Desktop */
  
  @media only screen and (min-width: 600px) {
  
    .navbar {
      top: 0;
      width: 5rem;
      height: 100vh;
    }
  
    .navbar:hover {
      width: 16rem;
    }
  
    .navbar:hover .link-text {
      display: inline;
      transition: opacity var(--transition-speed);
    }
  
  
  }
  /* NavBar End */